import React from "react";



const Square = (props) =>{
    return(
        <div
          onClick={props.onClick}
        style={{
            backgroundColor: "silver",
             border: "2.5px solid black" ,
         height:"100px",
         width:"100%",
          display:"flex",
          justifyContent:"center",
          alignItems:"center"
        }} 
        className="square">
        <h2>{props.value}</h2>
        </div>
    )
}

export default Square;